.lettre{
    width: 100%;
    height: 100%;

    
    
}

.form{
    margin-top: 100px;
  
}

.contact-form{
    margin-top: 30px;
}
.error::placeholder {
    color: #ff4d4d;
  }
#not-mail {
    display: none;
    position: absolute;
    top: -50%;
    color: #ff4d4d;
    right: 0;
    transform-origin: 50% 50%;
  }
  @keyframes dongle {
    0% {
      transform: translate(0px, 0px);
    }
    10% {
      transform: translate(-10px, 0px);
    }
    20% {
      transform: translate(10px, 0px);
    }
    30% {
      transform: translate(-10px, 0px);
    }
    40% {
      transform: translate(10px, 0px);
    }
    50% {
      transform: translate(-10px, 0px);
    }
    60% {
      transform: translate(10px, 0px);
    }
    70% {
      transform: translate(-10px, 0px);
    }
    80% {
      transform: translate(10px, 0px);
    }
    90% {
      transform: translate(-10px, 0px);
    }
    100% {
      transform: translate(0px, 0px);
    }
  }
  .form-message{
    margin-top: 2%;
  }

   

  @media (max-width: 991px) { 
    .form {
      margin-top: 200px;
  }
   }
   
   @media (max-width: 767px) { 
    .form {
      margin-top: 250px;
  }
   }
   
   @media (max-width: 433px) { 
    .form {
      margin-top: 280px;
  }
}